<template>
    <div class="row pt-5 mt-2">
        <img :src="$store.state.url_images+'store/store-smart.jpg'" alt="smart" class="img-fluid">
        <div class="col-4 p-1" v-for="(data, key) in products" :key="'product'+key">
            <router-link :to="{name:'Product',params:{kategori:data.kategori, kategori_id:data.id_kategory_barang, product_id:data.id_barang,product:data.seo_nama_barang}}">
                <div class="card product">
                    <img v-if="data.depan!=null&&data.depan!=''" class="lazy img-fluid card-img-top" :src="$store.state.url_images+'loading_post_produk.svg'" :data-src="$store.state.url_product+data.depan" :alt="data.nama_barang">
					<img v-else class="img-fluid card-img-top" :src="$store.state.url_images+'default_image.jpeg'" :alt="data.nama_barang">
                    <div class="card-body">
                        <p class="product-name small">{{data.nama_barang}}</p>
                        <strong class="small font-weight-bold text-success">
                            <template>
                                Rp{{toCurrency(data.harga_barang)}}
                            </template>
                        </strong>
                        <br>
                        <router-link :to="{name:'Store',params:{store:data.penjual_model.seo_nama_toko}}" class="small text-muted sell-name">{{data.penjual_model.nama_toko}}</router-link>
                    </div>
                </div>
            </router-link>
        </div>
    </div>
</template>
<script>
export default {
    name:"Product-Store",
    data(){
        return {
			busy:false,
			products:null,
			max:false
        }
    },
    async created(){
        this.products_store();
        window.addEventListener('scroll', this.loadMore)
    },
	destroyed () {
		window.removeEventListener('scroll',this.loadMore)
	},
    async updated(){
        await this.lazyLoad()
	},
    methods:{
		loadMore(){
			if(!this.busy){
				var d = document.documentElement;
				var offset = d.scrollTop + window.innerHeight;
				var height = d.offsetHeight;
			
				if (offset >= height-50) {
					this.products_store();
				}
			}
		},
        lazyLoad() {
			var lazyloadImages;

			if ("IntersectionObserver" in window) {
				lazyloadImages = document.querySelectorAll(".lazy");
				var imageObserver = new IntersectionObserver(function (entries) {
					entries.forEach(function (entry) {
						if (entry.isIntersecting) {
							var image = entry.target;
							image.src = image.dataset.src;
							image.classList.remove("lazy");
							imageObserver.unobserve(image);
						}
					});
				});

				lazyloadImages.forEach(function (image) {
					imageObserver.observe(image);
				});
            }
		},
        products_store(){
			if(!this.max){
				this.busy = true;
				let banyakProduct = this.products==null ? 0 : this.products.length;
				this.axios.get(this.$store.state.url_api+ 'merchants/'+this.$route.params.store+'/products/'+banyakProduct).then(response => {
					if(response.status==200){
						if(this.products==null){
							this.products = [];
						}
						response.data.data.products.forEach(element => {
							this.products.push(element);
						});
					}else if(response.status==204){
						this.max = true;
						if(this.products==null){
							this.products = [];
						}
					}else{
						// redirect ke page 404
					}
					this.busy = false;
				}).catch(()=> {
					this.busy = false;
				})
			}
		},
        toCurrency(angka) {
			angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
		},
    }
}
</script>